import message from '../../components/message'
import vButton from '../../components/button'

export default {
  components: { vButton },

  methods: {
    _success () {
      message.success('')
    },

    _error () {
      message.error('')
    },

    _info () {
      message.info('')
    },

    _customTime () {
      message.success(',10', 10)
    },

    _showLoading () {
      const hide = message.loading('...', 0)
      // 
      setTimeout(hide, 2500)
    }
  }
}


