import vButton from '../../components/button'
import vModal from '../../components/modal'
import message from '../../components/message'

const confirm = vModal.confirm
const info = vModal.info
const success = vModal.success
const error = vModal.error

export default {
  data () {
    return {
      modalText: '',
      confirmLoading: false,
      visible: false,
      visible2: false
    }
  },

  components: { vModal, vButton },

  methods: {
    _showModal () {
      this.visible = true
    },

    _handleOk () {
      message.success('')
      this.confirmLoading = false
      this.visible = false
    },

    _handleCancel () {
      message.info('')
      this.visible = false
    },

    _showModal2 () {
      this.visible2 = true
    },

    _handleOk2 () {
      message.success('')
      this.modalText = ''
      this.confirmLoading = true
      setTimeout(() => {
        this.visible2 = false
        this.confirmLoading = false
      }, 2000)
    },

    _handleCancel2 () {
      message.info('')
      this.visible2 = false
    },

    _showConfirm () {
      confirm({
        title: '',
        content: '',
        onOk: function() {
          console.log('')
        },
        onCancel: function() {}
      })
    },

    _showConfirm2 () {
      confirm({
        title: '',
        content: ' 1 ',
        onOk: function() {
          return new Promise(function(resolve) {
            setTimeout(resolve, 1000)
          })
        },
        onCancel: function() {}
      })
    },

    info() {
      info({
        title: '',
        content: '',
        onOk: function() {}
      })
    },

    success() {
      success({
        title: '',
        content: ''
      })
    },

    error() {
      error({
        title: '',
        content: ''
      })
    }
  }
}


